html,
body {
  height: 100%;
  font-family: "Roboto";
  color: $white;
}

.App {
  background-color: #0e0e0e;

  .particle-background {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  // dp = design pattern
  .dp-title {
    font-size: 48px;
    opacity: 0.8;
    color: $red;
  }

  .dp-subtitle {
    font-size: 36px;
    opacity: 0.8;
    color: $blue;

    &--secondary {
      font-size: 24px;
      opacity: 0.8;
      color: $yellow;
    }
  }

  .dp-section {
    padding: 16px;

    &--text {
      font-size: 16px;
      padding: 0 8px;
    }

    @include tablet {
      padding: 24px;

      &--text {
        font-size: 24px;
        padding: 0 16px;
      }
    }
  }

  .fade-in-section {
    opacity: 0;
    transform: translateY(20vh);
    visibility: hidden;
    transition: opacity 0.6s ease-out, transform 1.2s ease-out;
    will-change: opacity, visibility;
  }
  .fade-in-section.is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
  }
}
